.progress-bar {
  width: 100%;
  height: 20px;
  overflow: hidden;
  background-color: #dddddd;
  border-radius: 5px;
  margin: 20px 0;
}

.progress-bar__content {
  position: relative;
  height: 100%;
  background: #2aa8ff;
}

.progress-bar .percent {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  color: white;
}

.radio {
  padding-left: 20px;
}

.radio label {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  padding-left: 5px;
}

.radio label::before {
  content: "";
  display: inline-block;
  position: absolute;
  width: 17px;
  height: 17px;
  left: 0;
  margin-left: -20px;
  border: 1px solid #007ace;
  border-radius: 50%;
  background-color: white;
  transition: border 0.15s ease-in-out;
}

.radio label::after {
  display: inline-block;
  position: absolute;
  content: " ";
  width: 11px;
  height: 11px;
  left: 3px;
  top: 3px;
  margin-left: -20px;
  border-radius: 50%;
  background-color: #007ace;
  transform: scale(0, 0);
  transition: transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
}

.radio input[type="radio"] {
  opacity: 0;
  z-index: 1;
}

.radio input[type="radio"]:focus + label::before {
  outline: none;
}

.radio input[type="radio"]:checked + label::after {
  transform: scale(0.6, 0.6);
}

.tooltip-info {
